import React,{useContext,useState,useEffect} from 'react';
import { MyContext } from '../../contexts/MyContext';
import {Link } from "react-router-dom"; 

import { FiChevronsRight } from "react-icons/fi";
import Moment from 'react-moment';
import Navbarmenu from "../menu/Navbarmenu";
import Footer from "../Footer";
import PropertyPGEdittwo from "./PropertyPGEdittwo";
import '../../styles/_property.scss';


const PropertyPGEdit = (props) => {
  const {viewallsssallpg,allcategoriesss} = useContext(MyContext);
  useEffect(() => {
    viewallsss(props.match.params.AddPreview);
    catproimgsss(props.match.params.AddPreview);
    window.scrollTo(0, 0);
  }, []);

  const [isviewallsss, setviewallsss] = useState([]);
  const viewallsss = async(ids) =>{
    try {
      const viewallsssqqq = await viewallsssallpg(ids);
      if(viewallsssqqq.success === 1){
        console.log('viewallsssqqq',viewallsssqqq.viewalllist);
        setviewallsss(viewallsssqqq.viewalllist);
        return;
      }
    } catch (error) { throw error;}
  }
  const [iscat, setcat] = useState([]);
  const [isfac, setfac] = useState([]);
  const catproimgsss = async(ids) =>{
    try {
      const catproimgsssqqq = await allcategoriesss(ids);
      if(catproimgsssqqq.success === 1){
        console.log('catproimgsssqqq',catproimgsssqqq.catviews);
        setcat(catproimgsssqqq.catviews);
        setfac(catproimgsssqqq.facviews);
      }
    } catch (error) { throw error;}
  }
  return (
    <>
    <Navbarmenu />
  <div className="Rentsssviews"> 
    <div className="container"> 
      <div className="rows"> 
      {isviewallsss.length > 0 && iscat.length > 0 && isfac.length > 0 ? <> 
        <PropertyPGEdittwo listsss={isviewallsss}  catsss={iscat} faccc={isfac}/>
      </> : null }
      </div>
</div>
</div>

    <Footer />         
			  </>
  )
}

export default PropertyPGEdit
